<template>
	<cover-view class="xh-text-dialog" v-if="isShow">
		<cover-view class="xh-text-dialog-body" :class="animName">
			<!-- toast-header -->
			<cover-image class="xh-text-dialog-header" src="../static/images/xh-dialog-header.png"/>
			<!-- 弧线 -->
			<cover-view class="xh-text-dialog-arc">
				<cover-image class="xh-text-dialog-line" src="../static/images/dialog-line.png"/>
				<cover-image class="xh-text-dialog-icons" src="../static/images/volume-icon.png"/>
			</cover-view>
               <slot></slot>
			<!-- 背景 -->
			<cover-view class="xh-text-dialog-bg">
				<cover-image class="dialog-bg" src="../static/images/dialog-bg.png"></cover-image>
			</cover-view>
		</cover-view>
		<!-- 关闭按钮 -->
		<cover-image class="close fadeInOpacity" v-if="isShow" src="../static/images/toast_close.png" @click="close"/>
		<!-- 黑幕 -->
		<cover-view class="xh-text-dialog-black"  @touchmove.stop></cover-view>
	</cover-view>
</template>

<script>
	export default {
		props: {
			animName:{
				type:String,
				default:'xhDialog'
			}
		},
		data() {
			return {
				isShow: false
			}
		},
		methods: {
			show() {
				this.isShow = true;
			},
			close() {
				this.$emit('closeNotice')
				this.isShow = false;
			}
		}
	}
</script>

<style lang="scss">
	.xh-text-dialog {
		position: fixed;
		  top: 0;
		  left: 0;
		  width: 100%;
		  height: 100%;
		    z-index: 9999;
		.xh-msg-dialog{
				 position: relative;
				 z-index: 1;
				 padding: 0 RPX(38);
				 .msg{
					 padding-top: RPX(38);
					 font-size: RPX(25);
					 color: #fff;
					 text-align: center;
					 font-weight: bold;
				 }
				 .go-niuniu{
					 margin-top: RPX(80);
					 margin-bottom: RPX(20);
				 }
				 .msg-small{
					 font-size: RPX(13);
					 color: #F6E2B3;
					 padding-top: RPX(15);
					 padding: RPX(15) RPX(40) 0 RPX(40);
					 text-align: center;
				 }
		}	
			
			
		.xh-text-dialog-body {
			position: fixed;
			top: 50%;
			left: RPX(15);
			right: RPX(15);
			z-index: 1;
		}
		
		.xh-text-dialog-header{
			height: RPX(140);
			width: 70%;
			position: relative;
			z-index: 1;
			display: block;
			margin: 0 auto RPX(60);
		}
		.xh-text-dialog-arc{
			position: absolute;
			height: RPX(100);
			top:RPX(138);
			left: RPX(21);
			right: RPX(21);
		}
		
		.xh-text-dialog-icons{
			width: RPX(45);
			height: RPX(48);
			position: absolute;
			bottom: RPX(45);
			left: 50%;
			transform: translateX(-50%);
		}
		.xh-text-dialog-line{
			height: 30rpx;
			margin-top: 30rpx;
		}
		
		
	
		.xh-text-dialog-bg {
			position: absolute;
			left: RPX(21);
			right: RPX(21);
			bottom: 0;
			top: RPX(105);
			border-radius: 20px;
			z-index: -1;
			border: 3px solid #FFCB3E;
		}
		.dialog-bg{
			position: absolute;
			left: 0;
			right: 0;
			bottom: 0;
			top: 0;
		}
	
		.xh-text-dialog-black {
			position: fixed;
			left: 0;
			right: 0;
			bottom: 0;
			top: 0;
			background-color: $uni-bg-color-mask;
		}
	
		.close {
			width: RPX(40);
			height: RPX(40);
			position: fixed;
			bottom: 15%;
			left: 50%;
			transform: translateX(-50%);
			z-index: 1;
		}
	}
	
	@keyframes fadeInOpacity {
		0% {
			opacity: 0;
		}
	
		to {
			opacity: 1;
		}
	}
	
	.fadeInOpacity {
		-webkit-animation-name: fadeInOpacity;
		animation-name: fadeInOpacity;
		-webkit-animation-duration: 1s;
		-webkit-animation-fill-mode: both;
		animation-duration: 1s;
		animation-fill-mode: both;
	}
	
	@keyframes fadeInDownBig {
		0% {
			-webkit-transform: translate3d(0, -2000px, 0) translateY(-60%);
			opacity: 0;
			transform: translate3d(0, -2000px, 0) translateY(-60%);
		}
	
		to {
			-webkit-transform: translateZ(0) translateY(-60%);
			opacity: 1;
			transform: translateZ(0) translateY(-60%);
		}
	}
	
	.fadeInDownBig {
		-webkit-animation-name: fadeInDownBig;
		animation-name: fadeInDownBig;
		-webkit-animation-duration: 1s;
		-webkit-animation-fill-mode: both;
		animation-duration: 1s;
		animation-fill-mode: both;
	}
	/*自定义弹窗*/
	.xhDialog{
		-webkit-animation-name: xhDialog;
		animation-name: xhDialog;
		-webkit-animation-duration: 0.5s;
		-webkit-animation-fill-mode: both;
		animation-duration: 0.5s;
		animation-fill-mode: both;
	}
	
	@-webkit-keyframes xhDialog {
		0% {
			transform: scale(0.4);
			opacity: 0.3;
		}
	
		100% {
			transform: scale(1) translateY(-60%);
			opacity: 1;
		}
	}
	
</style>
